@use "base/variables" as v;
@use "base/mixins" as m;

.testimoniales{
    padding: v.$separacion 0;
    &__heading{
        text-align: center;
    }
    &__grid{
        @include m.tablet{
            @include m.grid(4,2rem);
        }
        @include m.desktop{
            @include m.grid(5,1rem);
        }
    }
}