@use "base/variables" as v;
@use "base/mixins" as m;

.testimonial{
    background-color: v.$secundario;
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 1rem;
    color: v.$blanco;
    &:last-of-type{
        margin-bottom: 0;
    }
    @include m.tablet{
        margin-bottom: 0;
        &:nth-child(odd){
            grid-column: 1/3;
        }
        &:nth-child(even){
            grid-column: 3/5;
        }
        &:nth-child(5){
            grid-column: 1/5;
        }
    }
    @include m.desktop{
        &:nth-child(1){
            grid-column: 1/4;
        }
        &:nth-child(2){
            grid-column: 4/5;
        }
        &:nth-child(3){
            grid-column: 5/6;
            grid-row: 1/3;
        }
        &:nth-child(4){
            grid-column: 1/3;
        }
        &:nth-child(5){
            grid-column: 3/5;
        }
    }
    &__header{
        display: flex;
        gap: 2rem;
        align-items: center;
    }
    &__imagen{
        flex-basis: 10rem;
        border-radius: 50%; 
        overflow: hidden;
    }
    &__informacion{
        padding-left: .5rem;
    }
    &__autor{
        color: v.$blanco;
        font-weight: 700;
        margin: .5rem 0;
    }
    &__texto{
        font-size: 2rem;
        line-height: 1.5;
        margin: 2rem 0 1rem 0;
        padding-left: 4rem;
        position: relative;
        &::before{
            content: ' " ';
            font-size: 10rem;
            position: absolute;
            top: -4rem;
            left: 0;
            opacity: .2;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        }
    }
}